package de.kampfkalender.web

import de.kampfkalender.web.pages.IndexPage
import de.kampfkalender.web.pages.PromotionsPage
import kotlinext.js.jso
import mui.material.Box
import mui.material.Container
import react.FC
import react.Props
import react.create
import react.dom.html.ReactHTML
import react.router.Outlet
import react.router.Route
import react.router.Routes

val Content = FC<Props> {
    // val showcases = useContext(ShowcasesContext)

    Container {
        Routes {
            Route {
                path = "/"
                element = Box.create {
                    component = ReactHTML.main
                    sx = jso {
                        // flexGrow = FlexGrow(1.0)
                        // padding = 30.px
                        marginTop = Styles.Header.Height
                    }
                    Outlet()
                }

                Route {
                    index = true
                    element = IndexPage.create()
                }

                Route {
                    path = "/promotions"
                    element = PromotionsPage.create()
                }
            }
        }
    }
}