private var EVENTS: List<ExportedEvent> = listOf()

private fun loadEvents(interval: DateInterval, missing: List<Date>, version: String): Promise<List<ExportedEvent>> {
    return Promise.all(
        // Build list of requests.
        buildList {
            if (isAfter(interval.start, interval.end)) {
                return@buildList
            }

            var i = 0
            // console.log("MISSING:", missing)
            // console.log("RANGE:", interval.start.toUTCString(), " => ", interval.end.toUTCString())
            while (true) {
                val stamp = addMonths(interval.start, i++)
                // console.log(stamp, " => ", zonedTimeToUtc(stamp, TIMEZONE))

                if (!isSameMonth(stamp, interval.end)
                    && stamp.getTime() > interval.end.getTime()
                    && stamp.getUTCMonth() != interval.end.getUTCMonth()
                ) {
                    // console.log("STOP", stamp.toUTCString(), "IS AFTER", interval.end.toUTCString())
                    break
                }
                //console.log(stamp, "not after", end)
                val isMissing = missing.any {
                    stamp.getUTCFullYear() == it.getUTCFullYear()
                            && stamp.getUTCMonth() == it.getUTCMonth()
                }
                if (isMissing) {
                    // console.log("IGNORE AT", stamp.toUTCString())
                    continue
                }
                // console.log("LOAD AT", stamp.toUTCString())
                add("/data/${stamp.getUTCFullYear()}/${dateDigit(stamp.getUTCMonth() + 1)}.json?v=${version}")
            }
        }
            // Execute each request and return a promise.
            .map { url: String ->
                window.fetch(url)
                    .then {
                        it.text()
                    }
                    .then {
                        Json.decodeFromString<List<ExportedEvent>>(it)
                    }
                    .catch {
                        listOf<ExportedEvent>()
                    }
            }.toTypedArray()
    )
        // Merge request results into a single list.
        .then {
            buildList {
                it.forEach {
                    addAll(it)
                }
            }
                .filter {
                    val i = jso<DateInterval> {
                        start = it.time.toDate()
                        end = it.until.toDate()
                    }
                    areIntervalsOverlapping(i, interval)
                }
                //.sortedWith(ExportedEvent.DEFAULT_SORT)
                .sorted()
        }
}

private fun getInterval(currentDate: Date, minDate: Date, maxDate: Date): DateInterval {
    return jso {
        start = max(arrayOf(currentDate, minDate))
        end = min(arrayOf(addDays(currentDate, 28), maxDate))
    }
}

val IndexPage = FC<Props> {
    // console.log("RENDER INDEX")
    val app = useContext(ApplicationContext)
    val minDate = app.summary?.getMinDate()
    val maxDate = app.summary?.getMaxDate()
    val version = ((app.summary?.lastUpdate?.toDate()?.getTime() ?: 0.0) / 1000).toString()
    if (minDate == null || maxDate == null) {
        Alert {
            sx = jso {
                marginTop = 6.rem
            }
            severity = AlertColor.warning
            AlertTitle {
                +"Schlechte Nachrichten..."
            }
            +"Es liegen gerade leider keine Termine vor. Bitte schaue später noch mal vorbei."
        }
        return@FC
    }

    val isSmall = useMediaQuery(SMALL_SIZE_MEDIA_QUERY)
    val (loading, setLoading) = useState(true)
    val (currentDate, setCurrentDate) = useState(Date())
    val (currentFilter, setCurrentFilter) = useState(FILTER_ALL)
    val (events, setEvents) = useState(listOf<ExportedEvent>())
    val filterEvents = {
        setEvents(EVENTS.filter { e: ExportedEvent ->
            if (currentFilter.free && !e.isFree()) {
                return@filter false
            }
            if (currentFilter.promotions.isNotEmpty()) {
                if (!currentFilter.promotions.contains(e.promotion)) {
                    return@filter false
                }
            }
            if (currentFilter.sports.isNotEmpty()) {
                if (!currentFilter.sports.any { e.sports.contains(it) }) {
                    return@filter false
                }
            }
            if (currentFilter.broadcasters.isNotEmpty()) {
                if (!currentFilter.broadcasters.any { e.broadcasters.contains(it) }) {
                    return@filter false
                }
            }
            return@filter true
        })
    }

    val (shownEvent, setShownEvent) = useState<ExportedEvent?>(null)

    // Current date was changed.
    useEffect(currentDate) {
        setLoading(true)
        loadEvents(
            getInterval(currentDate, minDate, maxDate),
            app.summary.getMissingDates(),
            version
        ).then {
            EVENTS = it
            filterEvents()

            // setLoading(false)
            window.setTimeout({ setLoading(false) }, 500)
        }
    }

    // Current filter was changed.
    useEffect(currentFilter) {
        filterEvents()
    }

    Box {
        sx = jso {
            display = Display.flex
            paddingTop = 1.em

            if (isSmall) {
                flexDirection = FlexDirection.column
            } else {
                flexDirection = FlexDirection.row
            }
        }

        //Tooltip {
        //    title = ReactNode("Ab diesem Datum werden dir alle Events der folgenden 28 Tage angezeigt.")
        TextField {
            sx = jso {
                if (isSmall) {
                    marginBottom = 1.em
                } else {
                    marginRight = 0.5.em
                    flexGrow = FlexGrow(0.0)
                }
            }

            size = if (isSmall) {
                Size.small
            } else {
                Size.medium
            }
            // fullWidth = true
            required = true
            type = InputType.date
            defaultValue = currentDate.toIsoDate()
            onInput = {
                // println("onChange ${value} => ${date}")
                val value = it.asDynamic().target.value as String
                val date = parseISO(value)
                setCurrentDate(date)
            }
            label = ReactNode("Events ab")
            InputLabelProps = jso {
                required = false
            }

            inputProps = jso {
                with(this.asDynamic()) {
                    this.min = minDate.toIsoDate()
                    this.max = maxDate.toIsoDate()
                }
            }
        }
        //}

        FilterSettings {
            filter = currentFilter
            setFilter = { setCurrentFilter(it) }
        }
    }

    Divider {
        sx = jso {
            marginTop = 1.em
            marginBottom = 1.em
        }
    }

    if (loading) {
        Box {
            sx = jso {
                textAlign = TextAlign.center
                marginTop = 2.em
            }
            CircularProgress()
        }
    } else if (events.isEmpty()) {
        Alert {
            severity = AlertColor.warning
            AlertTitle {
                +"Schlechte Nachrichten..."
            }
            +"Es wurden keine passenden Events gefunden. Wähle einen anderen Zeitraum oder prüfe mal die Filterkriterien."
        }
    } else {
        EventDialog {
            open = shownEvent != null
            event = shownEvent
            onClose = { setShownEvent(null) }
        }

        buildMap {
            events.forEach {
                val startDay = startOfDay(it.time.toDate())
                val endDay = startOfDay(it.until.toDate())
                val daysBetween = differenceInDays(startDay, endDay)

                for (i in 0..daysBetween) {
                    val day = addDays(startDay, i)
                    val key = day.toIsoDate()
                    val e = if (daysBetween == 0) {
                        it
                    } else {
                        it.copy(
                            time = day.toDateTime(),
                            until = if (isSameDay(day, it.until.toDate())) {
                                it.until
                            } else {
                                endOfDay(day).toDateTime()
                            }
                        )
                    }
                    this@buildMap.put(
                        key,
                        this@buildMap.getOrElse(key) { listOf<ExportedEvent>() }.plus(e)
                    )
                }
            }
        }.forEach {
            Schedule {
                val date = startOfDay(it.value.first().time.toDate())
                this.key = formatISO(date, jso {
                    format = "basic"
                    representation = "date"
                })
                this.day = date
                this.events = it.value
                this.onShowEvent = { e -> setShownEvent(e) }
            }
        }
    }
}
