package de.kampfkalender.web

import de.kampfkalender.common.Broadcaster
import de.kampfkalender.common.DateTime
import de.kampfkalender.common.ExportSummary
import de.kampfkalender.web.date.addDays
import de.kampfkalender.web.date.addMonths
import de.kampfkalender.web.date.formatISO
import de.kampfkalender.web.date.locale.DE
import de.kampfkalender.web.date.parseISO
import de.kampfkalender.web.date.tz.utcToZonedTime
import kotlinext.js.jso
import kotlin.js.Date

val TIMEZONE: String = js("Intl.DateTimeFormat().resolvedOptions().timeZone") as String

internal fun dateDigit(value: Int): String {
    return if (value < 10) {
        "0${value}"
    } else {
        value.toString()
    }
}

internal fun Date.toIsoDate(): String {
    return formatISO(this, jso {
        representation = "date"
    })
}

internal fun Date.toDateTime(): DateTime {
    return DateTime(
        "${this.getUTCFullYear()}-" +
                "${dateDigit(this.getUTCMonth() + 1)}-" +
                "${dateDigit(this.getUTCDate())}T" +
                "${dateDigit(this.getUTCHours())}:" +
                "${dateDigit(this.getUTCMinutes())}Z"
    )
}

internal fun DateTime.toDate(): Date {
    return utcToZonedTime(parseISO(this.iso), TIMEZONE)
}

internal fun DateTime.format(format: String): String {
    return this.toDate().format(format)
}

internal fun Date.format(format: String): String {
    return de.kampfkalender.web.date.format(this, format, jso {
        locale = DE
    })
}

internal fun ExportSummary.getMaxDate(): Date? {
    return if (this.dataEnd != null) {
        addDays(addMonths(parseYearMonth(this.dataEnd), 1), -1)
    } else {
        null
    }
}

internal fun ExportSummary.getMinDate(): Date? {
    return if (this.dataStart != null) {
        parseYearMonth(this.dataStart)
    } else {
        null
    }
}

internal fun ExportSummary.getMissingDates(): List<Date> {
    return this.dataMissing.map {
        parseYearMonth(it)
    }
}

