internal fun Broadcaster.getLogoHeight(): Int {
    return when (this) {
        Broadcaster.FITE_TV -> 20
        Broadcaster.GMC_TV -> 20
        Broadcaster.KSW_TV -> 15
        else -> 25
    }
}

private fun parseYearMonth(value : String): Date {
    val digits = value.split("-")
    return Date(Date.UTC(digits[0].toInt(), digits[1].toInt() - 1))
}