package de.kampfkalender.web.pages

import de.kampfkalender.common.Promotion
import mui.material.List as MaterialList
import mui.material.ListItem
import mui.material.Typography
import react.FC
import react.Props
import react.dom.html.ReactHTML
import react.useState

val PromotionsPage = FC<Props> {
    val (promotions, _) = useState<List<Promotion>>(Promotion.values().toList())

    Typography {
        component = ReactHTML.h1
        variant = "h2"
        +"Promotions"
    }

    MaterialList {
        promotions.forEach {
            ListItem {
                +"${it.title}"
            }
        }
    }
}
