package de.kampfkalender.web

import mui.material.List as MaterialList
import csstype.JustifyContent
import csstype.px
import csstype.vw
import de.kampfkalender.web.components.SidebarLink
import kotlinext.js.jso
import mui.icons.material.Close
import mui.icons.material.Mic
import mui.icons.material.Schedule
import mui.icons.material.Smartphone
import mui.icons.material.Tv
import mui.material.Divider
import mui.material.Drawer
import mui.material.DrawerAnchor
import mui.material.DrawerVariant
import mui.material.IconButton
import mui.material.IconButtonEdge
import mui.material.ListItem
import react.FC
import react.Props
import react.useContext

val Sidebar = FC<Props> {
    val app = useContext(ApplicationContext)

    Drawer {
        variant = DrawerVariant.temporary
        open = app.sidebarOpened
        // anchor = DrawerAnchor.left
        anchor = DrawerAnchor.right
        onBackdropClick = { app.setSidebarOpened(false) }

        MaterialList {
            sx = jso {
                // width = Sizes.Sidebar.Width
                width = 100.vw
                maxWidth = 250.px
            }

            ListItem {
                sx = jso {
                    justifyContent = JustifyContent.end
                    paddingTop = 0.px
                    paddingLeft = 0.px
                    paddingBottom = 4.px
                }

                IconButton {
                    // sx = jso { marginLeft = 8.px }
                    edge = IconButtonEdge.end
                    onClick = { app.setSidebarOpened(false) }
                    Close()
                }
            }

            Divider()

            SidebarLink {
                title = "Termine"
                target = "/"
                icon = Schedule
            }

            SidebarLink {
                title = "Abonnieren"
                target = "/subscribe"
                icon = Smartphone
            }

            Divider()

            SidebarLink {
                title = "Veranstalter"
                target = "/promotions"
                icon = Mic
            }

            SidebarLink {
                title = "Sender"
                target = "/broadcaster"
                icon = Tv
            }
        }
    }
}
